.ForecastList {
  list-style:none;
  display:flex;
  justify-content:space-between;
  align-items:center;
  margin-bottom:10px;
}
.date {
  margin-right:20px;
  white-space:nowrap;
}
.ForecastList div:not(.date) {
  font-size:7px;
  font-weight:500;
  display:flex;
  align-items:flex-end;
}